import { put } from 'redux-saga/effects';
import { delay } from 'redux-saga';

import Order from '../../axiosOrder';
import * as actionCreators from '../actions/index';

/**
 * JavaScript generator function that returns a Generator object
 * 
 * @param {*} action 
 */
export function* purchaseBurgerSaga(action) {
  yield put(actionCreators.purchaseBurgerStart());
  if (!action.token) {
    yield delay(2000);
    yield put(actionCreators.purchaseBurgerSuccess(
      'guestNoNameOrderNoUserId', action.orderData));
  } else {
    try {
      const response = yield Order.post('/orders.json', action.orderData,
        { params: { auth: action.token } });
      yield put(actionCreators.purchaseBurgerSuccess(response.data.name, action.orderData));
    } catch (error) {
      yield put(actionCreators.purchaseBurgerFail(error));
    }
  }
}

/**
 * JavaScript generator function that returns a Generator object
 * 
 * @param {*} action 
 */
export function* fetchOrdersSaga(action) {
  yield put(actionCreators.fetchOrdersStart());
  try {
    const response = yield Order.get('/orders.json', {
      params: {
        auth: action.token, orderBy: '"userId"', equalTo: `"${action.userId}"`
      }
    });
    const fetchedOrders = [];
    for (let key in response.data) {
      fetchedOrders.push({
        ...response.data[key],
        id: key
      });
    }
    yield put(actionCreators.fetchOrdersSuccess(fetchedOrders));
  } catch (error) {
    yield put(actionCreators.fetchOrdersFail(error));
  }
}


