import { delay } from 'redux-saga';
import { put, call } from 'redux-saga/effects';

import axios from '../../axiosFireBaseSignin';
import * as actionCreators from '../actions/index';

const TOKEN = 'token';
const EXPIRATION_DATE = 'expirationDate';
const USERID = 'userid';

/**
 * This is a JavaScript generator function that returns a Generator object
 * Generators are functions which can be exited and later re-entered
 * 
 * @param {*} action 
 * @returns generator function
 */
export function* logoutSaga(action) {
  yield call([localStorage, 'removeItem'], TOKEN);
  yield call([localStorage, 'removeItem'], EXPIRATION_DATE);
  yield call([localStorage, 'removeItem'], USERID);
  // yield localStorage.removeItem(TOKEN);
  // yield localStorage.removeItem(EXPIRATION_DATE);
  // yield localStorage.removeItem(USERID);
  yield put(actionCreators.logoutSucceed());
}

/**
 * JavaScript generator function that returns a Generator object
 * 
 * Use Redux Saga delay as setTimeout, then when time is up, yield logout 
 * action which invokes logoutSaga
 * 
 * @param {*} action 
 */
export function* checkAuthTimeoutSage(action) {
  yield delay(action.expirationTime * 1000);
  yield put(actionCreators.logout());
}

/**
 * JavaScript generator function that returns a Generator object
 * 
 * @param {*} action 
 */
export function* authUserSaga(action) {
  yield put(actionCreators.authStart());
  const authData = {
    email: action.email,
    password: action.password,
    returnSecureToken: true
  };
  try {
    const response = yield axios.post('/verifyPassword', authData);
    const expirationDate = yield new Date((new Date()).getTime() + response.data.expiresIn * 1000);
    yield localStorage.setItem(TOKEN, response.data.idToken);
    yield localStorage.setItem(EXPIRATION_DATE, expirationDate);
    yield localStorage.setItem(USERID, response.data.localId);
    yield put(actionCreators.authSuccess(response.data.idToken, response.data.localId));
    yield put(actionCreators.checkAuthTimeout(response.data.expiresIn));
  } catch (error) {
    yield put(actionCreators.authFail(error));
  }
}

/**
 * JavaScript generator function that returns a Generator object
 * 
 * @param {*} action 
 */
export function* authCheckStateSaga(action) {
  const token = yield localStorage.getItem(TOKEN);
  const userId = yield localStorage.getItem(USERID);
  if (token && userId) {
    let expirationDate = yield localStorage.getItem(EXPIRATION_DATE);
    if (expirationDate) {
      expirationDate = yield new Date(expirationDate);
      if (expirationDate <= (new Date())) {
        yield put(actionCreators.logout());
      } else {
        yield put(actionCreators.authSuccess((token, userId)));
        yield put(actionCreators.checkAuthTimeout((expirationDate.getTime() - (new Date()).getTime()) / 1000));
      }
    }
  } else {
    yield put(actionCreators.logout());
  }
}



