import { put } from 'redux-saga/effects';

import Order from '../../axiosOrder';
import SignIn from '../../axiosFireBaseSignin';
import * as actionCreators from '../actions/index';

/**
 * JavaScript generator function that returns a Generator object
 * 
 * @param {*} action 
 */
export function* initIngredientsSaga(action) {
  try {
    const token = yield SignIn.post('/verifyPassword', {
      email: 'wontonrich@firebase.com',
      password: 'reader!@123456',
      returnSecureToken: true
    });
    const response = yield Order.get('/ingredients.json', {
      params: { auth: token.data.idToken }
    });
    yield put(actionCreators.setIngredients(response.data));
  } catch (error) {
    yield put(actionCreators.fetchedIngredientsFailed());
  }
}


