import { takeEvery, all, takeLatest } from 'redux-saga/effects';

import * as actionTypes from '../actions/actionTypes';
import {
  logoutSaga,
  checkAuthTimeoutSage,
  authUserSaga,
  authCheckStateSaga
} from './auth';
import { initIngredientsSaga } from './burgerBuilder';
import { purchaseBurgerSaga, fetchOrdersSaga } from './order';

/**
 * Return a generator function
 * listen to auth initiate logout action, and then execute logoutSaga
 * listen to auth check timeout action, and then execute checkAuthTimeoutSage
 * listen to auth user action, and then execute authUserSaga
 * @returns generator function
 */
export function* watchAuth() {
  yield all([
    takeEvery(actionTypes.AUTH_CHECK_TIMEOUT, checkAuthTimeoutSage),
    takeEvery(actionTypes.AUTH_INITIATE_LOGOUT, logoutSaga),
    takeEvery(actionTypes.AUTH_USER, authUserSaga),
    takeEvery(actionTypes.AUTH_CHECK_STATE, authCheckStateSaga),
  ]);
}

/**
 * Return a generator function
 * listen to init set ingredient action, and then execute initIngredientsSaga
 * @returns generator function
 */
export function* watchBurgerBuilder() {
  yield takeEvery(actionTypes.INIT_INGREDIENTS, initIngredientsSaga);
}

/**
 * Return a generator function
 * listen to purchase order action, and then execute purchaseBurgerSaga
 * listen to fetch order action, and then execute fetchOrdersSaga
 * @returns generator function
 */
export function* watchOrder() {
  yield takeLatest(actionTypes.PURCHASE_BURGER, purchaseBurgerSaga);
  yield takeEvery(actionTypes.FETCH_ORDERS, fetchOrdersSaga);
}



