import * as actionTypes from './actionTypes';

// import axios from '../../axiosFireBaseSignin';

// const TOKEN = 'token';
// const EXPIRATION_DATE = 'expirationDate';
// const USERID = 'userid';

export const authStart = () => {
  return {
    type: actionTypes.AUTH_START
  };
};

export const authSuccess = (token, userId) => {
  return {
    type: actionTypes.AUTH_SUCCESS,
    token: token,
    userId: userId
  };
};

export const authFail = (error) => {
  return {
    type: actionTypes.AUTH_FAIL,
    error: error
  };
};

export const logout = () => {
  // localStorage.removeItem(TOKEN);
  // localStorage.removeItem(EXPIRATION_DATE);
  // localStorage.removeItem(USERID);
  return {
    type: actionTypes.AUTH_INITIATE_LOGOUT
  };
};

export const logoutSucceed = () => {
  return {
    type: actionTypes.AUTH_LOGOUT
  };
};

/**
 * Async actiion creator - logout after 60 mins
 * Leverage Redux Saga to track expiration time interval
 * @see {@link '../sagas/auth.js' checkAuthTimeoutSage}
 * @param {*} expirationTime - in millisecond
 */
export const checkAuthTimeout = (expirationTime) => {
  // return (dispatch) => {
  //   setTimeout(() => { dispatch(logout()); }, expirationTime * 1000)
  // };
  return {
    type: actionTypes.AUTH_CHECK_TIMEOUT,
    expirationTime: expirationTime
  };
};

